import React from "react"
import {graphql} from 'gatsby'
import Layout from "../components/layout"
import ArticleList from "../components/articleList"

const ArticleListByYM = ({ data,pageContext }) => {
    return (
        <Layout pageTitle={`貼文年月: ${pageContext.ym}`}>
            <ArticleList mdxNodes={data.allMdx.nodes} />
        </Layout>
    )
}

export default ArticleListByYM


export const query = graphql`
    query articlesInYM($dtBegin:Date, $dtEnd:Date){
        allMdx(
                sort: {fields: [frontmatter___date,slug] , order: [DESC,DESC] } 
                filter: {frontmatter: {date: {gte: $dtBegin, lte: $dtEnd}}}
            ) {
            nodes {
                id, slug, 
                frontmatter {
                    date(formatString: "YYYY/MM/DD")
                    title
                    shortDesc
                    category
                    tags
                    heroImg{
                        childImageSharp {
                          gatsbyImageData
                        }
                    }
                    heroImgAlt
                }
            }
        }
    }
`
